// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy","Playfair Display", sans-serif;
  --content-width: 1470px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --red-color: #FF1D1D;
  --gray-color: #E0E5EC;
  --bg-color: #1E2025;
  --dark-color:#27292E;
}
