.header{
    position: absolute;
    width: 100%;
    z-index: 100;
    top: 0;
    left: 0;
    color: #fff;

  &__container{
    position: relative;
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 50px;
  }

  // &__name{
  //   display: block;
  //   margin: 0;
  //   font-size: 25px;
  //   font-weight: 800;
  //   line-height: 25px;
  //   text-align: right;
  //   color: #fff;
  // }
  &__logo{
    position: absolute;
    top: 0px;
    border-radius: 100%;
    border: 3px solid #718096;
    left: 20px;
    width: 70px;
    height: 70px;
    z-index: 200;
  }

  &__right{
    margin-right: 20px;
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__phone-box{
    position: relative;
  }
  &__phone{
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: right;
    color: #fff;
  }

  &__text{
    margin: 0;
    position: absolute;
    top: 0px;
    right: 0;
    font-size: 13px;
    font-weight: 400;
    line-height: 18.2px;
    text-align: right;
  }

  &__text-hidden{
    display: none;
    margin: 0;
    width: 0;
    height: 0;
  }
  &__tel-hidden{
    display: none;
    margin: 0;
    width: 0;
    height: 0;
  }
  &__btn-hidden{
    display: none;
    width: 0;
    height: 0;
  }
  &__btn-box{
    display: flex;
    gap: 10px;
  }
 &__btn {
    font-size: 20px;
    font-weight: 600;
    line-height: 30.4px;
    color: #fff;
    text-transform: uppercase;

    &-red{
      color: #A80D0F;
      border-bottom: #A80D0F;
    }
}

  &__btn_active{
    color: #A80D0F;
    border-bottom: 1px solid #A80D0F;
  }
  &__btn:hover{
    color: #A80D0F;
  }

  .menu {
    &__icon {
      display: none;
      @media (max-width: 991px) {
        display: block;
        width: 30px;
        height: 18px;
        position: relative;
        z-index: 5;
        cursor: pointer;
        span {
          position: absolute;
          top: calc(50% - 1px);
          left: 0;
          width: 100%;
          height: 2px;
          background-color: #fff;
          transition: all 0.2s;
          &:first-child {
            top: 0;
          }
          &:last-child {
            top: auto;
            bottom: 0;
          }
        }
        &.active {
          span {
            transform: scale(0);
            &:first-child {
              transform: rotate(-45deg);
              top: calc(50% - 1px);
            }
            &:last-child {
              transform: rotate(45deg);
              bottom: calc(50% - 1px);
            }
          }
        }
      }
    }
    &__body {
      @media (max-width: 991px) {
        position: fixed;
        top: -120%;
        left: 0;
        height: 100vh;
        width: 100%;
        transition: all 0.2s;
        z-index: 2;
      }
      &.active {
        top: -30px;
      }
    }

    &__list {
      list-style-type: none;
      display: flex;
      gap: 20px;
      // margin-top: 20px;
      @media (max-width: 991px){

        flex-direction: column;
        padding: 80px 30px 20px 30px;
        width: 100%;
        background-color: #232123;
        overflow: auto;
      }


    }
   &__link {
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: left;
    color: #fff;
    &:hover{
      color: #A80D0F;
    }
    @media (max-width: 991px){
      font-size: 16px !important;
      font-weight: 700;
      line-height: 20px !important;
    }


      @media (max-width: 991px) {
        display: block;
        font-size: 30px;
        line-height: 70px;
      }
    }
  }

}
@media (max-width: 1250px){
  .header__right{
   display: none;
  }
}
 @media (max-width: 991px){
  .header{
    padding-top: 20px;
  }
  .header__container{
    justify-content: space-between;
  }
  .header__logo{
    top: -15px;
    width: 60px;
    height: 60px;
  }
  // .header__name{
  //   position: absolute;
  //   top: 0px;
  //   left: 20px;
  //   z-index: 101;
  // }
  .header__btn-box{
    position: absolute;
    right: 100px;
    top: -3px;
    z-index: 101;
  }

  .header__text-hidden{
    display: block;
    margin: 0;
    font-size: 11px;
    font-weight: 500;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: #718096;

  }
  .header__tel-hidden{
    display: block;
    margin-top: 10px;
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-weight: 600;
    line-height: 20px;
    letter-spacing: 0em;
    text-align: left;
    color: var(--light-color);
  }
  .header__btn-hidden{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 1px solid var(--red-color);
    margin-top: 20px;
    width: 320px;
    height: 48px;
    font-size: 15px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: center;
    color: var(--light-color);
  }
}

@include small-tablet{
  .header__container{
    padding: 0px 20px;
  }
}
@include mobile{
  .header__name{
    font-size: 20px;
  }
  .header__btn-hidden{
    width: 280px;
  }

}
