.feedback{

  padding: 50px 0px;

  &__title{
    margin: 0 auto;
    font-size: 44px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0em;
    text-align: center;
    margin-bottom: 50px;

    span{
      color: var(--red-color
      );
    }
  }
  &__line{
    position: relative;
    &::after{
      content: '';
      position: absolute;
      top: 60px;
      left: 55px;
      width: 119px;
      height: 3px;
      background-color: var(--red-color);
    }
  }

  .slider{
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    width: 1200px;
    height: 430px;
  }
  .slider-inner{
    position: relative;
    border-radius: 5px;
    padding: 15px 25px;
    width: 309px;
    height: 296px;
    background-color: #F7F8F9;
  }
  .slider__logo-box{
    width: 100px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .slider-name{
    margin: 0;
    font-size: 25px;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
  }
  .slider-wrap{
    display: flex;
    gap: 10px;
    align-items: center;
  }
  .slider-star{
    display: inline-block;
  }
  .slider-icon{
    display: block;
    width: 16.48px;
    height: 15.75px;
    fill: #A80D0F
  }
  .slider-descr{
    margin: 0;
    font-size: 14px;
    line-height: 18px;
    max-width: 290px;
    padding-top: 10px;
    font-weight: 400;
    text-align: left;
    color: #7B7D7E;
  }

  .slider-arrow{
    display: block;
    width: 30px;
    height: 20px;
    fill: #7B7D7E;
    position: absolute;
    right: 80px;
    bottom: 10px;
  }
  .slider-date{
    margin: 0;
    position: absolute;
    bottom: 10px;
    padding-top: 20px;
    color: #7B7D7E;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: left;
  }
  .prev{
    position: absolute;
    left: 450px;
    top: 390px;
    z-index: 10;
    border-radius: 30px;
    width: 40px;
    height: 40px;
    background-image: url('../img/prev.svg');
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    line-height: 50px;

    &::after{
      display: none;
    }
  }
  .next{
    position: absolute;
    right: 450px;
    top: 390px;
    z-index: 10;
    border-radius: 30px;
    width: 38px;
    height: 38px;
    background-image: url('../img/next.svg');
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    line-height: 50px;

  &::after {
    display: none;
    }
  }
  .swiper-pagination-bullet{
    background-color: #A80D0F;
  }

}

@media (max-width: (1199px)){
  .slider{
    margin: 0 auto;
    width: 800px !important;
  }
  .prev{
    position: absolute;
    left: 150px !important;
  }
  .next{
    position: absolute;
    right: 150px !important;
  }

  .slider-descr{
    margin: 0;
    font-size: 12px !important;
    line-height: 16px;
    max-width: 270px !important;
    padding-top: 10px;
    font-weight: 400;
    text-align: left;
    color: #7B7D7E;
  }

}

@media (max-width: (992px)){
  .slider{
    margin: 0 auto;
    width: 360px !important;
    height: 425px !important;
  }
  .feedback__title{
    font-size: 29px;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;

    .slider-inner{
      padding: 10px 20px;
      width: 340px;
      height: 425px;
    }
  }

  .prev{
    position: absolute;
    top: 380px !important;
    left: 30px !important;
  }
  .next{
    position: absolute;
    top: 380px !important;
    right: 30px !important;
  }
  .pagination{
    position: absolute;
    left: -50px !important;
    top: 380 !important;

  }

}
@media (max-width: (675px)){
  .pagination{
    position: absolute;
    left: 0px !important;
    top: 370 !important;

  }
}
@media (max-width: (400px)){
   .slider{
    width: 300px !important;
    height: 425px !important;
  }

  .prev{
    position: absolute;
    top: 380px !important;
    left: 10px !important;
  }
  .next{
    position: absolute;
    top: 380px !important;
    right: 0px !important;
  }

  .slider-inner{
    padding: 10px 20px;
    width: 300px;
    height: 296px;
    background-color: #F7F8F9;

    // &:not(:last-child){
    //   padding-right: 30px;
    // }

  }
}
