.hero-side{

  &__container{
    padding: 200px 50px 50px 50px;
    position: relative;
    background-image: url('../img/hero-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &__title{
    margin: 0;
    max-width: 790px;
    font-family: Playfair Display;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    text-align: left;
    color: #fff;
  }
  &__subtitle{
    margin: 0;
    color: #fff;
    font-family: Playfair Display;
    font-size: 71px;
    font-weight: 800;
    line-height: 85.2px;
    text-align: left;
    margin-bottom: 20px;
  }
  &__descr{
    margin: 0;
    max-width: 518px;
    font-family: Nunito Sans;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;
    color: #fff;
    margin-bottom: 20px;
  }
  &__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 278px;
    height: 66px;
    border-radius: 60px;
    background-color: #A80D0F;
    color: #fff;
    margin-bottom: 20px;
  }
  &__text{
    margin: 0;
    color: #fff;
    font-family: Playfair Display;
    font-size: 18px;
    font-weight: 600;
    line-height: 25.2px;
    text-align: left;
    margin-bottom: 10px;
  }
  &__list{
    position: absolute;
    right: 100px;
    bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  &__item{
    display: flex;
    align-items: flex-end;
    gap: 20px;
    width: 100%;
    height: 100%;
    width: 278px;
    height: 168px;
    background-image: url('../img/hero-card.png');
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 12px;
    padding: 20px;
    position: relative;
    cursor: pointer;
}
  &__item-next{
    width: 278px;
    height: 168px;
    background-image: url('../img/hero-card-2.png');
    border-radius: 12px;
  }
  &__time{
    margin: 0;
    color: #fff;
    font-family: Nunito Sans;
    font-size: 13px;
    font-weight: 400;
    line-height: 18.2px;
    text-align: left;
  }
  &__excursion{
    margin: 0;
    color: #fff;
    font-family: Playfair Display;
    font-size: 18px;
    font-weight: 600;
    line-height: 25.2px;
    text-align: left;
  }

  &__icon{
    display: block;
    fill: #fff;
    display: block;
    width: 18px;
    height: 18px;
  }

}

@include tablet{
   .hero-side__list{
    margin-top: 20px;
    position: static;
    gap: 10px;
    padding-bottom: 20px;
  }
  .hero-side__item{
    width: 200px;
    height: 120px;
  }
  .hero-side__time{
    font-size: 11px;
  }
  .hero-side__excursion{
    font-size: 16px;
    line-height: 18px;
  }

}
@include small-tablet{
  .hero-side__container{
    padding: 100px 20px 0px 20px !important;
  }
}
@include mobile{
  .hero-side__title{
   max-width: 250px;
  }
  .hero-side__subtitle{
    font-size: 50px;
    line-height: 50px;
  }
}

