.excursions{

  &__container{
    padding: 100px 50px;
  }
  &__title{
    margin: 0;
    color: #232123;
    font-family: Playfair Display;
    font-size: 49px;
    font-weight: 700;
    line-height: 49px;
    text-align: center;
    margin-bottom: 50px;
  }
  &__list{
    margin: 0 auto;
    max-width: 1260px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }
  &__item{
    position: relative;
    padding:10px;
    border-radius: 10px;
    max-width: 298px;
    min-height: 440px;
    -webkit-box-shadow: 0px -1px 23px 15px rgba(221,222,230,1);
    -moz-box-shadow: 0px -1px 23px 15px rgba(221,222,230,1);
    box-shadow: 0px -1px 23px 15px rgba(221,222,230,1);

  }

  .btn-center {
    grid-column: 2 / 4 span;
    text-align: center;
  }
  .show-more{
    margin-top: 50px;
    width: 278px;
    height: 66px;
    color: #fff;
    border-radius: 60px;
    background-color: #A80D0F;
    font-family: Playfair Display;
    font-size: 18px;
    font-weight: 600;
    line-height: 25.2px;
    text-align: center;
  }

  &__item:nth-child(n + 9) {
    display: none;
  }

  &__item.is-visible {
    display: block;
  }
  &__img{
    width: 278px;
    height: 212px;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  &__text{
    margin: 0;
    text-transform: uppercase;
    max-width: 258px;
    color: #232123;
    font-family: Playfair Display;
    font-size: 20px;
    font-weight: 500;
    line-height: 20px;
    text-align: left;
    margin-bottom: 10px;
    padding-left: 10px;
  }
  &__day{
    margin: 0;
    text-transform: uppercase;
    color: #7B7D7E;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;
    margin-bottom: 5px;
    padding-left: 10px;
  }
  &__price{
    margin: 0;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: left;
    color: #7B7D7E;
    margin-bottom: 10px;
    padding-left: 10px;

    span{
    padding-left: 10px;
    color: #A80D0F;
    font-size: 25px;
    font-weight: 600;
    line-height: 30px;
    text-align: left;
    }
  }
  &__btn{
    position: absolute;
    bottom: 10px;
    left: 10px;
    width: 95%;
    height: 50px;
    border-radius: 60px;
    border: 2px solid #A80D0F;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: center;
    color: #A80D0F;
  }
}
@media (max-width: (1200px)){
  .excursions__list{
    max-width: 940px;
    grid-template-columns: repeat(3, 1fr);
  }
}
@include tablet {
  .excursions__title{
    font-family: Playfair Display;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    text-align: center;
  }
  .excursions__container{
    padding: 50px 20px;
  }
  .excursions__list{
    max-width: 620px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@include small-tablet{
  .excursions__list{
    max-width: 350px;
    grid-template-columns: repeat(1, 1fr);
  }
  .excursions__item{
    padding:0px;
    border-radius: 10px;
    max-width: 350px;
    min-height: 460px;
  }

  .excursions__img{
    width: 350px;
    height: 250px;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  .excursions__text{
    padding-left: 20px;
  }
  .excursions__day{
    padding-left: 20px;
  }
  .excursions__price{
    padding-left: 20px;
  }
  .excursions__flag{
    position: absolute;
    top: 260px;
    right: 0px;
    width: 60px;
    height: 50px;
  }
}
