.works{


  // padding: 50px 0px;

  &__title{
    margin: 0 auto;
    max-width: 800px;
    position: relative;
    font-size: 44px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0em;
    text-align: center;
    margin-bottom: 50px;

    &::after{
      content: '';
      position: absolute;
      left: 300px;
      top: 60px;
      width: 174px;
      height: 2px;
      background-color: #A80D0F;
    }
  }


  &__img{
    width: 380px;
    height: 280px;
    border-radius: 14px;
  }
  .swiper {
    width: 1200px;
    height: 360px;
  }
  .swiper-inner{
    width: 380px;
    height: 280px;
    border-radius: 14px;
  }
  .swiper-button-prev{
    position: absolute;
    z-index: 10;
    left: 400px;
    top: 340px;
    border-radius: 30px;
    width: 40px;
    height: 40px;
    background-image: url('../img/prev.svg');
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    line-height: 60px;

    &::after{
      display: none;
    }
  }
  .swiper-button-next{
    position: absolute;
    z-index: 10;
    right: 400px;
    top: 340px;
    border-radius: 30px;
    width: 40px;
    height: 40px;
    background-image: url('../img/next.svg');
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    line-height: 60px;

  &::after {
    display: none;
    }
  }
  .swiper-pagination-bullet{
    background-color: #A80D0F;
  }
}

@media (max-width: (1199px)){
  .swiper{
    width: 800px !important;
  }
  .swiper-button-prev{
    position: absolute;
    left: 150px !important;
  }
  .swiper-button-next{
    position: absolute;
    right: 150px !important;
}
}
@include tablet{
  .swiper{
    margin: 0 auto;
    width: 500px !important;
    height: 385px !important;
  }
  .swiper-inner{
    margin: 0 auto;
    width: 500px;
    height: 400px;
    border-radius: 14px;
  }
  .swiper__img{
    width: 500px;
    height: 400px;
    border-radius: 14px;
}


  .swiper-button-prev{
    position: absolute;
    top:365px !important;
    left: 90px !important;
  }
  .swiper-button-next{
    position: absolute;
