.graph-modal__close{
  background-repeat: no-repeat;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  width: auto;
  height: auto;
  right: 20px;
  top: 20px;
  background-image: none;
  color: #232123;
}

.popup{
  max-width: 800px;
  min-height: 500px;
  border-radius: 20px;
  padding-top: 30px;
}
.popup-img{
  background-size: contain;
  margin: 0 10px 5px 0;
  width: 200px;
  height: 200px;
  border-radius: 20px;
  float: left;
}
.popup-title{
  margin: 0;
  color: #232123;
  font-family: Playfair Display;
  font-size: 31px;
  font-weight: 700;
  line-height: 41px;
  text-align: left;
  margin-bottom: 10px;
}
.popup-subtitle{
  margin: 0;
  color: #7B7D7E;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  text-align: left;
  margin-bottom: 10px;
  text-transform: uppercase;
}
.popup-descr{
  margin: 0;
  color: #232123;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  text-align: left;
  margin-bottom: 10px;
  text-transform: lowercase ;
}

.popup-inner{
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 20px;
}
.popup-price-title{
  position: relative;
  margin: 0;
  color: #232123;
  font-family: Playfair Display;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  text-align: left;
  margin-bottom: 5px;

  &::after{
    content: '';
    position: absolute;
    top: 25px;
    left: 0px;
    width: 80px;
    height: 1px;
    background-color: #A80D0F;
  }
}

.popup-price-sub{
  margin: 0;
  text-transform: uppercase;
  color: #7B7D7E;
  font-size: 13px;
  font-weight: 400;
  margin-bottom: 5px;
  text-align: left;
}
.popup-bottom{
  display: flex;
}
.popup-price-text{
  margin: 0;
  text-transform: uppercase;
  color: #7B7D7E;
  font-size: 13px;
  font-weight: 400;
  line-height: 18.2px;
  text-align: left;
  margin-bottom: 10px;
}
.popup-price{
  margin: 0;
  color: #232123;
  font-size: 13px;
  font-weight: 400;
  line-height: 18.2px;
  text-align: left;
  margin-bottom: 5px;

}
.popup-total{
  margin: 0;
  color: #A80D0F;
  font-size: 18px;
  font-weight: 600;
  line-height: 25.2px;
  text-align: left;
}
.popup-link{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 50px;
  border: 2px solid #A80D0F;
  border-radius: 60px;
  color: #A80D0F;
  font-family: Playfair Display;
  font-size: 16px;
  font-weight: 600;
  line-height: 22.4px;
}
.tabs__nav{
  margin-bottom: 20px;
  gap: 10px;
  flex-wrap: wrap;
}
.tabs__nav-btn{
  font-family: Playfair Display;
  font-size: 26px;
  font-weight: 600;
  line-height: 40px;
  color: #000;
}
.tabs__nav-btn:focus {
  color: #000;
  background-color: #fff;

}
.tabs__nav-btn--active{
  border-bottom: 2px solid #A80D0F;

}

@include tablet{
  .popup-inner{
   display: flex;
   flex-wrap: wrap;
   gap: 10px;
  }
  .popup-img{
    width: 350px;
    height: 280px;
    margin-bottom: 10px;
  }
  .tabs__nav-btn{
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
  }
  .popup-title{
    font-size: 20px;
    line-height: 31px;
  }
}


