.footer{
  background-color: var(--dark-color);

  &__container{
    padding: 50px 0px 30px 0px;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 50px;
    color: #fff;
  }
  &__logo-box{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap: 10px;
  }
  &__logo{
    border-radius: 100%;
    border: 3px solid #718096;
    width: 70px;
    height: 70px;
  }
  &__logo-text{
    margin: 0;
    color: #7B7D7E;
    font-family: Nunito Sans;
    font-size: 13px;
    font-weight: 400;
    line-height: 18.2px;
    text-align: left;
  }
  &__list{
    max-width: 310px;
  }
  &__item{
    margin-bottom: 10px;
  }
  &__link{
    color: var(--light-color);
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: left;
    transition: all 0.5s ease-out;

    @include hover{
      color: #A80D0F;
    }
  }
  &__right{
    max-width: 150px;
    margin-top: -10px;
    flex-direction: column-reverse;
    align-items: flex-start;
    gap: 5px;
  }
  &__btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 278px;
    height: 50px;
    background-color: #A80D0F;
    color: #fff;
    border-radius: 60px;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    cursor: pointer;
  }
  &__phone-box{
    position: relative;
    margin-top: 10px;
  }
  &__text{
    margin: 0;
    text-align: right;
    color: #fff;
    font-family: Nunito Sans;
    font-size: 13px;
    font-weight: 400;
    line-height: 18.2px;
  }
  &__tel{
    display: block;
    color: #fff;
    font-family: Nunito Sans;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: right;
  }
  &__social-text{
    margin: 0;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: left;
    margin-bottom: 10px;
  }
}
@include tablet{
  .footer__container{
    padding: 20px;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .footer__right{
    margin-top: 10px;
    display: block;
  }
  .footer__list{
    margin-top: 10px;
  }
  .footer__text{
    position: absolute;
    top: -15px;
    left:0;
    font-size: 12px;
  }
  .footer__right{
    display: none;
  }
  .footer__social-box{
    margin-bottom: 10px;
  }
}
