.presentation{
  padding: 50px 0px;

  &__title{
    position: relative;
    max-width: 400px;
    margin: 0 auto;
    padding-bottom: 50px;
    color: #232123;
    font-family: Playfair Display;
    font-size: 49px;
    font-weight: 700;
    line-height: 49px;

    &::after{
      content: '';
      position: absolute;
      top: 60px;
      left: 40px;
      width: 174px;
      height: 2px;
      background-color: #A80D0F;
    }
  }

  &__video{
    position: static;
    display: block;
    margin: 0 auto;
    max-width: 790px;
    height: 400px;
    border-radius: 14px;
  }
}
@include tablet{
  .presentation__title{
    max-width: 200px;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    text-align: center;
    &::after{
      content: '';
      position: absolute;
      top: 40px;
      left: 40px;
      width: 120px !important;
      height: 2px;
      background-color: #A80D0F;
    }

  }
.presentation__video{
  max-width: 450px;
  height: 300px;
}
}
@include mobile{
  .presentation__video{
    margin: 0 auto;
    bottom: none;
    // margin-left: -5px;
    padding: 0;
    max-width: 380px;
    height: 240px;
  }
}
@media (max-width: (400px)){
  .presentation__video{
    max-width: 300px;
    height: 240px;
  }
}
