.tours{
  padding-bottom: 50px;


  &__title{
    margin: 0 auto;
    max-width: 500px;
    text-align: center;
    font-family: Playfair Display;
    font-size: 49px;
    font-weight: 700;
    line-height: 49px;
    text-align: center;
    padding: 50px 0px 30px 0px;
    margin-bottom: 30px;
    position: relative;


    &::after{
      content: '';
      position: absolute;
      top: 110px;
      left: 160px;
      width: 174px;
      height: 2px;
      background-color: #A80D0F;
    }
  }
  &__list{
    margin: 0 auto;
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;
  }
  &__item{
    position: relative;
    grid-column: 6 span;
    background-image: url('../img/card-1.png');
    padding: 50px;
    width: 585px;
    height: 415px;
    border-radius: 20px;

    &:nth-child(2){
      background-image: url('../img/card-2.png');
    }

    &:nth-child(3){
      background-image: url('../img/card-3.png');
    }

    &:nth-child(4){
      background-image: url('../img/card-4.png');
    }

    &:nth-child(5){
      width: 1200px;
      grid-column: 12 span;
      background-image: url('../img/card-5.png');
    }
  }
  &__link{
    display: block;
    width: 100%;
    height: 100%;
  }
  &__text{
    margin: 0;
    padding-top: 50px;
    color: #fff;
    font-family: Playfair Display;
    font-size: 39px;
    font-weight: 700;
    line-height: 39px;
    text-align: left;
    margin-bottom: 20px;
  }
  &__descr{
    margin: 0;
    max-width: 400px;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    text-align: left;

    &-bottom{
      max-width: 668px;
    }
  }
  &__btn{
    position: absolute;
    left: 50px;
    bottom: 50px;
    width: 320px;
    height: 50px;
    font-family: Playfair Display;
    font-size: 16px;
    font-weight: 600;
    line-height: 22.4px;
    text-align: center;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 60px;
    background-color: #A80D0F;

  }
}
@media (max-width: (1250px)) {
  .tours__list{
    max-width: 900px;
  }
  .tours__item{
    padding: 20px;
    width: 440px;
    height: 300px;
    &:nth-child(5){
      width: 900px;
    }

  }
  .tours__text{
    color: #fff;
    font-family: Playfair Display;
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
    margin-bottom: 20px;
  }
  .tours__descr{
    font-size: 14px;
  }
  .tours__btn{
    position: absolute;
    left: 50px;
    bottom: 20px;
  }
}
@include tablet{
  .tours__list{
   display: flex;
   flex-direction: column;
   align-items: center;
  }
  .tours__item{

    &:nth-child(5){
      width: 440px;
      height: 350px;
    }

  }
}
@include mobile{
  .tours__title{
    max-width: 300px;
    font-size: 32px;
    font-weight: 700;
    line-height: 32px;
    text-align: center;


    &::after{
      content: '';
      position: absolute;
      top: 100px;
      left: 70px;
      width: 150px;
      height: 2px;
      background-color: #A80D0F;
    }

  }

  .tours__item{
    width: 390px;
    height: 350px;
    &:nth-child(5){
      width: 390px;
      height: 350px;
    }
  }
  .tours__btn{
    width: 220px;
  }
}
@media (max-width: (400px)){
    .tours__item{
    width: 320px;
    height: 400px;
    &:nth-child(5){
      width: 320px;
      height: 400px;
    }
  }
}
